import React, { useState, useEffect } from 'react';

import { Modal } from 'antd';
import { ExclamationCircleOutlined } from '@ant-design/icons';
import { FileBrowser, FileContextMenu, FileList, FileNavbar, FileToolbar, ChonkyActions, defineFileAction, ChonkyIconName } from 'chonky';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import { TouchBackend } from 'react-dnd-touch-backend';
import moment from 'moment';

import withMyAlert from '../_shared/HOCs/withMyAlert';
import TextDialog from '../_shared/Modals/TextDialog';
import { IsMobile } from '../_utils/Display';
import * as _uri from '../_utils/UriHelper';

const { confirm } = Modal;
const axios = require('axios').default;

function FileManagement(props) {
  const renameAction = defineFileAction({
    id: "custom_rename",
    requiresSelection: true,
    button: {
      name: "R",
      toolbar: true,
      contextMenu: true,
      icon: ChonkyIconName.code
    }
  });
  const deleteAction = defineFileAction({
    id: "custom_delete",
    requiresSelection: true,
    button: {
      name: "D",
      toolbar: true,
      contextMenu: true,
      icon: ChonkyIconName.trash
    }
  });
  const moveAction = defineFileAction({
    id: "custom_move",
    requiresSelection: true,
    button: {
      name: "M",
      toolbar: true,
      contextMenu: true,
      icon: ChonkyIconName.folder
    }
  });
  const fileActions = [
    deleteAction,
    renameAction,
    moveAction,
    ChonkyActions.EnableGridView,
    ChonkyActions.EnableListView,
    ChonkyActions.UploadFiles,
    ChonkyActions.CreateFolder
  ];

  const handleAction = (data) => {
    if (data.id === "custom_delete") {
      console.log("delete", data);

      const cModel = data.state.selectedFiles[0];
      const typeText = cModel.isDir ? `directory` : 'file';

      confirm({
        title: `Delete ${typeText} ${cModel.name}?`,
        icon: <ExclamationCircleOutlined />,
        okText: 'YES',
        okType: 'danger',
        cancelText: 'NO',
        onOk() {
          const subDir = viewModel.dirChains.find(dir => dir.id === "1")?.name;
          console.log(`confirm delete ${cModel.name}, subDir ${subDir}, albumId ${props.id}`);

          const delObj = (() => {
            function compose(vAlbumId, vSubDir, vFileName) {
              return ({
                albumId: vAlbumId,
                subDir: vSubDir,
                fileName: vFileName
              });
            }
            if (cModel.isDir) {
              return compose(viewModel.albumId, cModel.name, "");
            }
            else {
              return compose(viewModel.albumId, subDir ?? "", cModel.name);
            }
          })();

          console.log("delete ", delObj);
          axios.delete(`${_uri.file}Delete?id=${delObj.albumId}&subDir=${delObj.subDir}&fileName=${delObj.fileName}`)
            .then(function (response) {
              const newFsNodes = (() => {
                if (delObj.subDir !== "" && delObj.fileName === "") {
                  return viewModel.fsNodes.filter(n => n.name !== delObj.subDir);
                }
                else if (delObj.subDir === "" && delObj.fileName !== "") {
                  return viewModel.fsNodes.filter(n => n.name !== delObj.fileName);
                }
                else if (delObj.subDir !== "" && delObj.fileName !== "") {
                  viewModel.fsNodes.forEach(n => {
                    if (n.name === delObj.subDir) {
                      n.childs = n.childs.filter(c => c.name !== delObj.fileName);
                    }
                  });
                  return viewModel.fsNodes;
                }
              })();

              setViewModel((prevState) => ({
                ...prevState,
                fsNodes: newFsNodes
              }));
            })
            .catch(function (error) {
              props.popApiError(error);
            });
        },
        onCancel() {
        },
      });
    }
    else if (data.id === "custom_rename") {
      console.log("rename", data);
      if (data.state.selectedFiles.length === 0) { return; }
      setTextDialog({
        visible: true,
        btnText: 'Rename',
        initialValue: data.state.selectedFiles[0].name,
        onOk: handleRename
      });
    }
    else if (data.id === "custom_move") {
      console.log("move", data);
    }
    else if (data.id === ChonkyActions.EndDragNDrop.id) {
      console.log("EndDragNDrop", data);

      if (data.payload.draggedFile.isDir) { return; }
      if (!data.payload.destination.isDir) { return; }

      const dir = data.payload.destination.name; //root or subDir
      const fileName = data.payload.draggedFile.name;

      const moveParam = {
        overwrite: false,
        src: {
          albumId: viewModel.albumId,
          subDir: viewModel.subDir,
          name: fileName
        },
        dst: {
          albumId: viewModel.albumId,
          subDir: dir === 'Root' ? null : dir,
          name: fileName
        }
      };
      postMoveFile(moveParam, adjustFsNodes);

      function adjustFsNodes(param) {
        const srcName = param.src.name;
        const srcFsNode = (() => {
          const srcDir = param.src.subDir;
          if (srcDir !== null) {
            const dirIdx = viewModel.fsNodes.findIndex(n => n.name === srcDir);
            const fileIdx = viewModel.fsNodes[dirIdx].childs.findIndex(f => f.name === srcName);
            const result = viewModel.fsNodes[dirIdx].childs.splice(fileIdx, 1)[0];
            return result;
          }
          else {
            const fileIdx = viewModel.fsNodes.findIndex(n => n.name === srcName);
            const result = viewModel.fsNodes.splice(fileIdx, 1)[0];
            return result;
          }
        })();

        const dstDir = param.dst.subDir;
        if (dstDir !== null) {
          const dirIdx = viewModel.fsNodes.findIndex(n => n.name === dstDir);
          if (param.overwrite) {
            const owIdx = viewModel.fsNodes[dirIdx].childs.findIndex(f => f.name === srcName);
            viewModel.fsNodes[dirIdx].childs.splice(owIdx, 1);
          }

          viewModel.fsNodes[dirIdx].childs.push(srcFsNode);
          viewModel.fsNodes[dirIdx].childs.sort(compare);
        }
        else {
          if (param.overwrite) {
            const owIdx = viewModel.fsNodes.findIndex(n => n.name === srcName);
            viewModel.fsNodes.splice(owIdx, 1);
          }
          viewModel.fsNodes.push(srcFsNode);
          viewModel.fsNodes.sort(compare);
        }

        setViewModel((prevViewModel) => {
          return ({
            ...prevViewModel
          });
        });

        function compare(a, b) {
          if (a.nodeType === b.nodeType) { //Make sure folders always come before files
            var nameA = a.name.toLowerCase(), nameB = b.name.toLowerCase();
            if (nameA < nameB) //sort string ascending
              return -1;
            if (nameA > nameB)
              return 1;
            return 0; //default return value (no sorting)
          }
          return a.nodeType > b.nodeType ? 1 : -1;
        }
      }
    }
    else if (data.id === ChonkyActions.UploadFiles.id) {
      console.log("Upload", data)
    }
    else if (data.id === ChonkyActions.OpenFiles.id) {
      if (!data.payload.targetFile.isDir) { return; }

      const targetSubDir = data.payload.targetFile.name !== "Root" ?
        data.payload.targetFile.name :
        null;
      const newDirChains = targetSubDir !== null ?
        [...viewModel.dirChains, { id: '1', name: targetSubDir, isDir: true }] :
        rootChain;

      setViewModel((prevViewModel) => {
        prevViewModel.subDir = targetSubDir;
        prevViewModel.dirChains = newDirChains;
        return ({
          ...prevViewModel
        });
      });
    }
    else if (data.id === ChonkyActions.OpenParentFolder.id) {
      console.log("OpenParentFolder", data)
    }
    else if (data.id === ChonkyActions.CreateFolder.id) {
      setTextDialog({
        visible: true,
        btnText: 'Create Folder',
        initialValue: '',
        onOk: handleCreateFolder
      });
    }
  };

  const postMoveFile = (param, onSuccess) => {
    axios.post(`${_uri.file}NewMoveFile`, param)
      .then(function (response) {
        console.log(response);
        if (response.data.message === "Destination file already exist") {
          confirm({
            title: `Overwrite?`,
            icon: <ExclamationCircleOutlined />,
            content: <FileComparison file1={response.data.srcInfo} file2={response.data.dstInfo} />,
            okText: 'YES',
            okType: 'danger',
            cancelText: 'NO',
            onOk() {
              const newParam = {
                ...param,
                overwrite: true
              }

              postMoveFile(newParam, onSuccess);
            },
            onCancel() {
            },
          });
        }
        else {
          console.log("Move success", response.data);
          onSuccess(param);
        }
      })
      .catch(function (error) {
        //debugger;
        props.popApiError(error);
      });
  };

  const defaultTextDialog = { visible: false, initialValue: 'ad', btnText: null };
  const [textDialog, setTextDialog] = useState(defaultTextDialog);
  const handleRename = (val, oldVal) => {
    //console.log("rename ok", val, oldVal);
    //console.log("textDialog", textDialog);

    const moveParam = {
      overwrite: false,
      src: {
        albumId: viewModel.albumId,
        subDir: viewModel.subDir,
        name: oldVal
      },
      dst: {
        albumId: viewModel.albumId,
        subDir: viewModel.subDir,
        name: val
      }
    };
    postMoveFile(moveParam, adjustFsNodes);
    function adjustFsNodes(param) {
      setViewModel((prev) => {
        if (param.src.subDir !== null) {
          const dirIdx = prev.fsNodes.findIndex(n => n.name === param.src.subDir);
          if (param.overwrite) {
            const owIdx = prev.fsNodes[dirIdx].childs.findIndex(f => f.name === param.dst.name);
            prev.fsNodes[dirIdx].childs.splice(owIdx, 1);
          }

          const fileIdx = prev.fsNodes[dirIdx].childs.findIndex(f => f.name === param.src.name);
          prev.fsNodes[dirIdx].childs[fileIdx].name = param.dst.name;
        }
        else {
          if (param.overwrite) {
            const owIdx = prev.fsNodes.findIndex(n => n.name === param.dst.name);
            prev.fsNodes.splice(owIdx, 1);
          }

          const fileIdx = prev.fsNodes.findIndex(n => n.name === param.src.name);
          prev.fsNodes[fileIdx].name = param.dst.name;
        }

        return ({
          ...prev
        });
      });
    }
  };
  const handleCreateFolder = (val, oldVal) => {
    console.log('folder name', val);
    setViewModel((prev) => {
      if (viewModel.subDir !== null) {
        props.popError("Invalid Operation", "Creating folder inside a folder is not allowed");
      }
      prev.fsNodes.unshift({
        name: val,
        size: 0,
        nodeType: 0,
        uncPathEncoded: null,
        childs: []
      });
      return ({
        ...prev
      });
    });
  };

  const rootChain = [{ id: '0', name: 'Root', isDir: true }];
  const [viewModel, setViewModel] = useState({
    albumId: null,
    title: '',
    subDir: null,
    fsNodes: [],
    dirChains: rootChain
  });

  useEffect(() => {
    if (props.id === "" || props.id === null || props.id === undefined) { return; }

    axios.get(`${_uri.file}GetAlbumFsNodes?id=${props.id}`)
      .then((response) => {
        setViewModel({
          albumId: props.id,
          title: response.data.title,
          subDir: null,
          fsNodes: response.data.fsNodes,
          dirChains: rootChain
        });

        console.log(response.data)
      })
      .catch((error) => {
        props.popApiError(error);
      });

  }, [props.id]);

  const [chonkyModels, setChonkyModels] = useState([]);
  useEffect(() => {
    const targetFsNodes = viewModel.subDir !== null ?
      viewModel.fsNodes.find(n => n.name === viewModel.subDir).childs :
      viewModel.fsNodes;

    const newModels = targetFsNodes.map((n, index) => {
      return {
        id: index + 1,
        name: n.name,
        isDir: n.nodeType === 0,
        thumbnailUrl: n.nodeType === 1 ? _uri.resizedPage(n.uncPathEncoded, 150) : null,
        size: n.size,
        childrenCount: n.childs?.length,
        selectable: true,
        draggable: n.nodeType !== 0
      }
    });

    setChonkyModels(newModels);
  }, [viewModel]);

  const dndBackend = IsMobile() ? TouchBackend : HTML5Backend;

  return (
    <>
      <div style={{
        width: "100%",
        padding: "0px 8px 0px 8px",
        borderTop: "1px solid grey",
        borderBottom: "1px solid grey",
        marginBottom: "2px"
      }}>
        <h3>{viewModel.title}</h3>
      </div>
      <div style={{ height: "80vh" }}>
        <DndProvider backend={dndBackend}>
          <FileBrowser
            darkMode={true}
            //disableSelection={true}
            files={chonkyModels} folderChain={viewModel.dirChains} fileActions={fileActions}
            disableDragAndDropProvider={true}
            disableDefaultFileActions={true}
            onFileAction={handleAction}
          >
            <FileNavbar />
            <FileToolbar />
            {/* <div style={{ width: "100%", }}>wololo</div> */}
            <FileList />
            <FileContextMenu />
          </FileBrowser>
        </DndProvider>
      </div>
      <TextDialog
        visible={textDialog.visible} onClose={() => setTextDialog(defaultTextDialog)}
        initialValue={textDialog.initialValue}
        onOk={textDialog.onOk}
        btnText={textDialog.btnText}
      />
    </>
  );
}

export default withMyAlert(FileManagement);


function FileComparison(props) {
  const f1 = props.file1;
  const f2 = props.file2;

  return (
    <table class="comparison-table" style={{ width: "100%" }}>
      <tr>
        <th>Source</th>
        <th>Destination</th>
      </tr>
      <tr>
        <td>{f1.name}</td>
        <td>{f2.name}</td>
      </tr>
      <tr>
        <td style={getStyle(f1.size, f2.size)}>{(f1.size / 1024).toFixed(2)} KB</td>
        <td style={getStyle(f2.size, f1.size)}>{(f2.size / 1024).toFixed(2)} KB</td>
      </tr>
      <tr>
        <td style={getStyle(f1.createdDate, f2.createdDate)}>{moment(f1.createdDate).format('YYYY-MM-DD hh:mm')}</td>
        <td style={getStyle(f2.createdDate, f1.createdDate)}>{moment(f2.createdDate).format('YYYY-MM-DD hh:mm')}</td>
      </tr>
    </table>
  );

  function getStyle(a, b) {
    const fontWeight = a > b ? "bold" : "normal";
    return {
      fontWeight: fontWeight
    };
  }
}