  //     console.log("callback", event.changedTouches[0]);
  //     if (Helper.global.ect0.screenY !== event.changedTouches[0].screenY) {
  //       console.log("not same", Helper.global.ect0.screenY, event.changedTouches);
  //     }
  //     else {
  //       console.log("same");
  //     }
  //     return;
  //   }
  //   handler.edit();
  // }, []);

  // const bind = useLongPress(longPressCallback, {
  //   onStart: event => {
  //     if (event.changedTouches && event.changedTouches[0]) {
  //       console.log("onStart", event.changedTouches[0]);
  //       Helper.global.ect0 = {
  //         pageX: event.changedTouches[0].pageX,
  //         screenY: event.changedTouches[0].screenY,
  //       };
  //       return;
  //     }
  //   },
  //   onFinish: event => { },
  //   onCancel: event => {
  //     //console.log("Press canceled", Helper.clickReport(event));
  //     if (event.changedTouches && event.changedTouches[0]) { return; }
