import { IS_PUBLIC, API_URL, STORAGE_URL, SAS_TOKEN } from "./Config";

const crudController = IS_PUBLIC ? "PublicCrud" : "Crud";
const fileController = IS_PUBLIC ? "PublicFile" : "File";
const crud = `${API_URL}${crudController}/`;
const file = `${API_URL}${fileController}/`;
const media = `${API_URL}Media/`;

//const placeholder = '[Sam] UFC Posters/71wnOWqFfTL._AC_SY679_.jpg';

const fullPage = (albumId, alRelPathBase64) => {
  if (IS_PUBLIC) {

    const isVideo = alRelPathBase64.endsWith(".mp4");
    const cachePrevention = isVideo ? `&xyz=${Number(new Date())}` : "";

    return `${STORAGE_URL}${albumId}/${alRelPathBase64}${SAS_TOKEN}${cachePrevention}`;
  }
  return `${media}StreamPage?albumId=${albumId}&alRelPathBase64=${alRelPathBase64}`;
}

const resizedPage = (librelPathBase64, maxSize) => {
  if (IS_PUBLIC) {
    return `${STORAGE_URL}${librelPathBase64}${SAS_TOKEN}`;
  }
  return `${media}StreamResizedImage?librelPathBase64=${librelPathBase64}&maxSize=${maxSize}`;
}

export { crud, file, fullPage, resizedPage };